<template>
    <div>
        <router-link to="zbiaoti" @click="asd">
            <ul id="fangkuai">
                <li>
                    <span id= "cc" class="iconfont icon-yonghu"></span>
                    <p>{{title}}</p>
                    <p id="riqi">{{riqi}}</p>
                </li>
                <li>
