                        <p><b>{{fubiaoti}}</b></p>
                        <p style="font-size:15px">{{wenzi}}</p>
                    </div>
                    <img :src="imgadd" alt="" />

                </li>
            </ul>
        </router-link>
    </div>
</template>

<script>
import router from "../router/index.js" 
export default {
    name: 'fangkuai',
    data() {
        return {
        };
    },
    props:{
        imgadd:{
            type:String,
            required:true,
        },
        title:{
            type:String,
            required:false,
        },
        riqi:{
