            required:false,
        },
        fubiaoti:{
            type:String,
            required:false,
        },
        wenzi:{
            type:String,
            required:false,
        },
    },
    mounted() {
    },
    methods: {
        asd(){
            router.replace({name:"zbiaoti",params:{id:this.title,title1:this.fubiaoti,content:this.wenzi}})
        },
    },
};
</script>

