<template>
    <div class="box">
    <ul id="tou">
      <router-link to="/">
        <li>&lt;</li>
      </router-link>
      <li>404页面丢失</li>
    </ul>
    <ul id="tupian">
      <li v-for="(v,i) in list" :key="i">
        <img :src="v.pic">
      </li>
    </ul>
    </div>
</template>
<script>
import {getCurrentInstance,reactive,onMounted} from 'vue';
import { Toast } from 'vant';
    export default {
        components:{
           [Toast .name]: Toast, 
        },
        name:"error1",
        setup() {
            var list=reactive([]) 
            //数组和对象用reactive 其他用ref
            var {ctx}=getCurrentInstance()

            onMounted(()=>{
                ctx.$axios.get("http://localhost:3000/data").then(res=>{
                   console.log(res.data);
                    list.push(...res.data); 
                }) 
                Toast.fail('没找到您访问的网页，请返回首页！');
            });
            return {
               list
            }
        }
    }
</script>
<style scoped>
#tou{
    width: 100%;
    height: 50px;
    background: green;
    display: flex;
  }
  #tou li:nth-child(1){
    padding-top: 15px;
    padding-left: 15px;
    width: 10%;
    height: 35px;
    color: red;
  }
  #tou li:nth-child(2){
      color: red;
      width: 90%;
      text-align: center;
      padding-top: 13px;
  }
  #tupian{
    width: 100%;
  }
  #tupian img{
    width: 100%;
    height: 300px;
  }
</style>