import { createStore } from 'vuex'
export default createStore({
    state: {
        count: 36600
    },
    mutations: {
        add(state){
            state.count++;
        },
        addn(state,action){
            state.count+=action.n;
        }
    },
    actions: {
       adds(ctx){
           setTimeout(()=>{
            ctx.commit("addn",{n:100}); 
           },3000);
       }
    },
    getters: { 
       
    }
})