import { createRouter, createWebHashHistory } from 'vue-router'
import App from '../App.vue'
import index from '../components/index.vue'
import bnav from '../components/bnav.vue'
import lunbo from '../components/lunbo.vue'
import indexpart2 from '../components/indexpart2.vue'
import kecheng from '../views/kecheng.vue'
import lunbo1 from '../components/lunbo1.vue'
import shequ from '../views/shequ.vue'
import sousuotou from '../components/sousuotou.vue'
import fangkuai from '../components/fangkuai.vue'
import shequshang from '../components/shequshang.vue'
import wo from '../components/wo.vue'
import wode from '../views/wode.vue'
import bingxue from '../views/bingxue.vue'
import error from '../components/error.vue'
import xiangqing from '../components/xiangqing.vue'
import xqk from '../components/xqk.vue'
import zuowen from '../views/zuowen.vue'
import zdaohang from '../components/zdaohang.vue'
import zbiaoti from '../components/zbiaoti.vue'
import error1 from '../components/error1.vue'
import vuex from '../components/vuex.vue'
import fuqin from '../components/fuqin.vue'
import zi from '../components/zi.vue'
const router = createRouter({
     history: createWebHashHistory(),
    routes: [
        {
            path: '/App',
            component: App
        },
        {
            path: '/zi',
            component: zi
        },
        {
            path: '/fuqin',
            component: fuqin
        },
        {
            path: '/error1',
            component: error1,
            name:"error1",
            alias:"/cuowu"
        },
        {
            path: '/vuex',
            component: vuex,
            name:"vuex"
        },
        {
            path: '/zbiaoti/:id/:title1/:content',
            component: zbiaoti,
            name:'zbiaoti'
        },
        {
            path: '/zdaohang',
            component: zdaohang
        },
        {
            path: '/zuowen',
            component: zuowen
        },
        {
            path: '/xqk',
            component: xqk
        },
        {
            path: '/wode',
            component: wode,
            name:"wode"
        },
        {
            path: '/wo',
            component: wo
        },
        {
            path: '/xiangqing',
            component: xiangqing,
            name:"xiangqing"
        },
        {
            path: '/fangkuai/:imgadd',
            component: fangkuai
        },
        {
            path: '/shequshang',
            component: shequshang
        },
        {
            path: '/sousuotou',
            component: sousuotou
        },
        {
            path: '/shequ',
            component: shequ
        },
        {
            path: '/indexpart2',
            component: indexpart2
        },
        {
            path: '/',
            component: index
        },
        {
            path: '/bnav',
            component: bnav
        },
        {
            path: '/lunbo',
            component: lunbo
        },
        {
            path: '/kecheng',
            component: kecheng
        },
        {
            path: '/lunbo1',
            component: lunbo1,
        },
        {
            path: '/bingxue',
            component: bingxue,
        },
        {   path: '/error',
            component: error,
            name:'error',
        },
        {
            path: '/:pathMatch(.*)*',
            component: error1,
        }
        
    ]
})
export default router
