<template>
    <div class="daaq">
        <h1>lhy vuex</h1>
        <h2 @click="plus">点击增加：{{count}}</h2>
        <button @click="plus">add 1</button>
        <button @click="plusn">add 10</button>
        <button @click="plus100">3s latter add 100</button>
    </div>
</template>
<script>
    import { computed} from 'vue';
    import {useStore} from 'vuex';

    export default {
        name: 'vuex',
        setup() {
            const store = useStore();
            var count= computed(()=>store.state.count)

            function plus(){
               store.commit("add"); 
            }
            function plusn(){
               store.commit("addn",{n:10}); 
            }
            function plus100(){
               store.dispatch("adds",{n:100}); 
            }
            //mutation 不能有异步操作如ajax
            //action调用异步操作调用mutation
            return {
               count,plus,plusn,plus100
            }
        }
    }
</script>
<style scoped>
    .daaq{
        margin: 0 auto;
    }
</style>